import { toRefs, ref } from 'vue'

export default {
  // 侧边导航栏折叠事件
  menuCollapseHooks: {} as Record<number, () => any>,
  menuCollapseHooksExecute() {
    Object.keys(this.menuCollapseHooks).forEach(uid => {
      const func = Reflect.get(this.menuCollapseHooks, uid)
      func()
    })
  },
  onMenuCollapse(hook: () => any, uid: number) {
    Reflect.set(this.menuCollapseHooks, uid, hook)
  },
  menuCollapseHookRemove(uid: number) {
    delete this.menuCollapseHooks[uid]
  },

  // 查询的股票信息
  searchedStockInfo: {
    stock_id: '',
    stock_publisher: '',
  },
  // 自选股票
  stockFocus: ref([]),
  // 用户信息
  userInfo: ref({
    avatar: ''
  }),

}
