
export const typeToString = (instance: any): string => {
  // eg: "[object Number]"
  return Object.prototype.toString.call(instance)
}

export const isNumber = (instance: any): boolean => {
  return typeToString(instance) === '[object Number]'
}

/**
 * 获取当前日的下一天的时间戳
 */
export const getNextDate = (date: Date, day: number): number => {
  const dd = new Date(date);
  dd.setDate(dd.getDate() + day);
  return dd.getTime()
}

export const getCookie = (name: string) => {
  let arr; const reg = new RegExp('(^| )' + name + '=([^;]*)(;|$)');
  if (arr = document.cookie.match(reg)) {
     return unescape(arr[2]);
  } else { return null; }
};

export const setCookie = (name: string, value: string, time: any) => {
  const exp = new Date();
  exp.setTime(exp.getTime() + time);
  document.cookie = name + '=' + escape(value) + ';expires=' + (exp).toUTCString() + ';path=/';
};

export const delCookie = (name: string) => {
  const exp = new Date();
  exp.setTime(exp.getTime() - 1000 * 60 * 60 * 24 * 30);
  const cval = getCookie(name);
  if (cval != null) { document.cookie = name + '=' + cval + ';expires=' + (exp as any).toGMTString() + ';path=/'; }
};

export const getPublisherByStockId = (stock_id: string) => {
  console.log(stock_id[0])
  return stock_id[0] === '6' ? 'sh' : 'sz'
}




