<template>
  <div class="stock-wrapper">

    <nav
      class="stock-nav"
      :class="{
        collapse: isCollapse,
      }">
      <el-menu
        background-color="var(--color-primary)"
        text-color="var(--color-primary-light)"
        active-text-color="#fff"
        router
        :default-active="activeRoute"
        :collapse="isCollapse"
        :translate="false">
        <el-menu-item
          v-for="(item) in menuItems"
          :key="item.text"
          :index="item.route"
          :route="item.route">
          <i :class="item.icon"></i>
          <template #title>
            {{ item.text }}
          </template>
        </el-menu-item>
      </el-menu>
    </nav>

    <main
      class="stock-main-content"
      :class="{
        collapse: isCollapse,
      }">

      <div class="stock-status-bar">
        <div class="stock-nav-collapse-btn">
          <i
            :class="['el-icon-s-'+(isCollapse ? 'unfold' : 'fold')]"
            @click="collapseChange"></i>
          {{ currentRoute }}
        </div>
        <div class="stock-message-btn-group">
          <i class="el-icon-message-solid"></i>
          <i class="el-icon-s-comment"></i>
          <el-avatar :src="GlobalData.userInfo.value.avatar">
          </el-avatar>
        </div>



      </div>

      <div class="stock-main">
        <router-view />
      </div>

    </main>

  </div>
</template>

<script lang="ts">
import axios from 'axios';
import { defineComponent, onMounted, ref, toRef, watch } from 'vue'
import { useRoute } from "vue-router";
import { ElMenu, ElMenuItem, ElAvatar } from 'element-plus'

import GlobalData from '@/store/index'
import { ROUTE_CONSOLE_DASHBOARD, ROUTE_CONSOLE_PROFILE,
  ROUTE_CONSOLE_STOCKLIST, ROUTE_LOGIN, ROUTE_REGISTER } from './router/config'
import { autoLogin_api } from './store/api';
import { getCookie } from './utils/func';

export default defineComponent({
  components: {
    ElMenu,
    ElMenuItem,
    ElAvatar,
  },
  setup() {
    // 导航栏
    const menuItems = [
      {
        text: 'Dashboard',
        icon: 'el-icon-s-help',
        route: ROUTE_CONSOLE_DASHBOARD.use()
      },
      {
        text: 'Profile',
        icon: 'el-icon-user-solid',
        route: ROUTE_CONSOLE_PROFILE.use()
      },
      {
        text: 'StockList',
        icon: 'el-icon-menu',
        route: ROUTE_CONSOLE_STOCKLIST.use()
      },
      {
        text: 'Login',
        icon: 'el-icon-coordinate',
        route: ROUTE_LOGIN.use()
      },
      {
        text: 'Register',
        icon: 'el-icon-position',
        route: ROUTE_REGISTER.use()
      },
    ]
    const activeRoute = ref(menuItems[0].route)
    // 根据路由，得到当前页标题
    const $route = useRoute()
    const currentRoute = ref('')
    watch($route, () => {
      activeRoute.value = $route.path
      const title = $route.path.split('/').pop() as string
      currentRoute.value = title[0].toUpperCase() + title.slice(1)
    })
    // 导航栏是否折叠
    const isCollapse = ref(false)
    const collapseChange = () => {
      isCollapse.value = !isCollapse.value
      GlobalData.menuCollapseHooksExecute()
    }
    const autoLogin = async () => {
      const token = getCookie('token')
      if(!token) return
      axios.defaults.headers.Authorization = `Token ${token}`
      const res = await axios.post(autoLogin_api, {token})
      GlobalData.userInfo.value = res.data.data
    }

    onMounted(() => {
      autoLogin()
    })

    return {
      menuItems,
      currentRoute,
      isCollapse,
      collapseChange,
      activeRoute,
      GlobalData,
    }

  }
})

</script>

