    &.collapse {
      width: 64px;
    }

  }

  .stock-main-content {
    width: calc(100% - 14rem);
    max-height: 100vh;
    overflow: auto;
    transition: width .2s;
    &.collapse {
      width: calc(100% - 64px);
    }
  }

  .stock-status-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    // width: 100%;
    background-color: white;
    height: 4.375rem;
    position: relative;
    box-shadow: 0 .15rem 1.75rem 0 rgba(58,59,69,.15);
    font-size: 1.3rem;
    padding: 0 30px 0 15px;

    .stock-nav-collapse-btn {


      & > i {
        cursor: pointer;
      }

    }

    .stock-message-btn-group {
      display: flex;
      align-items: center;
      i {
        cursor: pointer;
        -webkit-background-clip:text;
        -webkit-text-fill-color:transparent;
        margin-right: 20px;
      }
      i:first-child {
        background-image: linear-gradient(0, #ffe259, #ffa751);

      }

      i:nth-child(2) {
        background-image: linear-gradient(300deg, rgb(172, 204, 234), rgb(100, 149, 237));
      }
    }



  }

  .stock-main {
    padding: 1.5rem;
  }

}


</style>
